//page about
.about {
	padding-top: 60px;
	@include mq($max-width: 767px) {
		padding-top: 20px; }
	h2 {
		color: black;
		font-weight: bold;
		font-size: 32px;
		line-height: 48px;
		text-transform: uppercase;
		text-align: center;
		margin-bottom: 30px;
		@include mq($max-width: 767px) {
			margin-bottom: 20px; } }
	.about__block {
		display: flex;
		background: #F3F3F3;
		padding: 30px 53px;
		gap: 104px;
		margin-bottom: 30px;
		@include mq($max-width: 768px) {
			flex-direction: column;
			gap: 20px; }
		@include mq($max-width: 767px) {
			padding: 15px; }
		.about__contact {
			display: flex;
			flex-direction: column;
			gap: 40px;
			a {
				display: flex;
				align-items: center;
				gap: 23px;
				color: black;
				font-weight: 500;
				font-size: 20px;
				line-height: 20px;
				@include mq($max-width: 767px) {
					font-size: 18px; } } }
		form {
			display: flex;
			flex-direction: column;
			max-width: 477px;
			width: 100%;
			@include mq($max-width: 768px) {
				max-width: 100%; }
			label {
				font-weight: 500;
				font-family: Poppins;
				font-style: normal;
				font-size: 16px;
				line-height: 24px;
				color: #3E3E3E;
				margin-bottom: 10px;
				margin-top: 10px; }
			textarea {
				resize: none;
				background: inherit;
				border: 1.5px solid #E0E0E0;
				padding: 10px;
				font-weight: 500;
				font-family: Poppins;
				font-style: normal;
				font-size: 16px;
				line-height: 24px;
				color: #3E3E3E;
				min-height: 120px;
				height: 100%;
				margin-bottom: 20px;
				@include mq($max-width: 768px) {
					max-width: 100%; } }
			button {
				display: flex;
				justify-content: end;
				font-family: Poppins;
				font-style: normal;
				font-weight: 500;
				font-size: 16px;
				line-height: 24px;
				cursor: pointer;
				color: #3E3E3E; }
			input {
				border: none;
				background: inherit;
				font-weight: 500;
				font-family: Poppins;
				font-style: normal;
				font-size: 16px;
				line-height: 24px;
				color: #3E3E3E;
				width: 100%;
				max-width: 100%;
				padding-bottom: 10px;
				padding-top: 10px;
				margin-bottom: 10px;
				border-bottom: 1.5px solid #E0E0E0;
				&::placeholder {
					font-family: Poppins;
					font-style: normal;
					font-weight: 500;
					font-size: 16px;
					line-height: 24px;
					color: #3E3E3E; } } } } }
